import MasterPage from "../components/Master";
import fetch from 'isomorphic-unfetch';
import Precio from "../components/Precio";
import Noticias from "../components/Noticias";
import Eventos from "../components/Eventos";

const Index = (props) => (  
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio 
                precio={props.precioBitcoin}
                />
            </div>

            <div className="col-md-8">
                <h2 className="mb-3">Noticias sobre el Bitcoin</h2>
                <Noticias
                noticias={props.noticiasBitcoin}
                />
            </div>

            <div className="col-md-4">
                <h2 className="mb-3">Pr√≥ximos Eventos Bitcoin</h2>
                <Eventos 
                eventos={props.eventosBitcoin}
                />
            </div>
        </div>
    </MasterPage>
);
Index.getInitialProps =  () => {
    const precio = fetch('https://api.coinmarketcap.com/v2/ticker/1',{ mode: 'no-cors' });
    const noticias = fetch('https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=6e54ab2f704c4e3ca56dc29114143465&language=es');
    const eventos = fetch('https://www.eventbriteapi.com/v3/events/search/?q=bitcoin&sort_by=date&location.address=Mexico&token=4ZJH4VUQEYMW4RSISIA3');

    const resPrecio = precio.json();
    const resNoticias = noticias.json();
    const resEventos = eventos.json();

    return {
        precioBitcoin : resPrecio.data.quotes.USD,
        noticiasBitcoin : resNoticias.articles,
        eventosBitcoin : resEventos.events
    }
}
 
export default Index;